<template>
  <div @mouseenter="shakes">
  <el-card style="float:left;border: 2px solid #cccccc; margin-bottom:3px; height:100px;">

    <div class="news">
      <!---->
      <div class="smallPic"><img :src="news.newsPic" :alt="news.title" style="margin:5 auto;height:70px;width:100%;float:left;"></div>

      <div style="position:relative;top:-3px;">
        <!--  -->
        <div class="list-hd">
          {{news.title}}
        </div>
        <div class="otherInfo">
            <span class="other-left">
                <span :onload="timeago(news.publishTime)">{{time}}</span>
                <span>  {{news.publisher}}</span>
                <span>  {{news.newsReadNum}}</span>
            </span>
        </div>
      </div>
    </div>

  </el-card>
</div>
</template>
<script>
export default{
  data(){
    return{
      time:'2017-10-31 17:17:00',
      homeTeam:'',
      visitTeam:''
    }
  },
  props:['news'],
  methods:{
    timeago(time){   //dateTimeStamp100013
            var minute=1000*60;      //
            var  hour=minute*60;
            var day=hour*24;
            var week=day*7;
            var halfamonth=day*15;
            var month=day*30;

            var  now=new Date().getTime();   //
            var dateTimeStamp = Date.parse(new Date(time));

            console.log('timeago'+now);
            console.log('timeago'+dateTimeStamp);
            var diffValue=now-dateTimeStamp;//

            if(diffValue<0){return;}

            var  minC=diffValue/minute;  //
            var  hourC=diffValue/hour;
            var  dayC=diffValue/day;
           var  weekC=diffValue/week;
            var  monthC=diffValue/month;
            var result;
            console.log(minC);
           if(monthC>=1){
             result=" "+parseInt(monthC)+""
              }

         else  if(weekC>=1){
             result=" "+parseInt(weekC)+""
              }
        else    if(dayC>=1){
             result=" "+parseInt(dayC)+""
              }
       else    if(hourC>=1){
             result=" "+parseInt(hourC)+""
              }
       else    if(minC>=1){
             result=" "+parseInt(minC)+""
              }
        else{
        result="";
        }
let that=this;
that.time=result;
     return result;
   },
   shakes(e){
     e = (e.targetclassName= 'imgSmall')? e.target :null;
    if (!time) var time=650;
    if (!distance) var distance=4;
    var originalStyle=e.style.cssText;
    e.style.position='relative';
    var start=(new Date()).getTime();
    animate();
    function animate(){
      var now=(new Date()).getTime();
      var elapsed=now-start;
      var fraction=elapsed/time; //time 
      if (fraction<1)
      {
       var x=distance*Math.sin(fraction*4*Math.PI);
        e.style.left=x+'px';
        setTimeout(animate,Math.min(25,time-elapsed));
      }
      else
      {
        e.style.cssText=originalStyle;
      }
    }
  },
 },
}
</script>

<style scoped>
.news{
  float:left;
  width:740px;
  height: 90px;
  margin: 5px;
  /*border-style: inherit;*/
}
.list-hd{
  position: relative;
  /*top:-0px;*/
  font-weight: bold;
  font-size: 20px;
}
.other-left{
  color: gray;
  position: relative;
  top: 10px;
}
.smallPic{
  float:left;
  margin:-10px 10px 0px 0px;
  width:124px;
  border-style: inset;
  border-radius: 4px;
  border-color: black;
}
</style>



