<template>
  <div>
    <el-container>

      <el-header style="padding:0;">
        <!--  -->
        <myHeader></myHeader>
      </el-header>

      <el-main>
        <el-container style="margin-top:30px;">
          <el-aside width="200px" style="margin-left:100px;margin-right:30px;">
            <el-menu
            router
              default-active="1"
              style="height:100%;"
              class="el-menu-vertical-demo"
              @open="handleOpen"
              @close="handleClose"
              background-color="#DCDFE6"
              text-color=""
              active-text-color="black">

              <el-menu-item index="1" :route="{path:'/CenterIndex/UserInfoManager'}">NBA</el-menu-item>
              <el-menu-item index="2" :route="{path:'/CenterIndex/ReviewManager'}">CBA</el-menu-item>
              <el-menu-item index="3" :route="{path:'/CenterIndex/ReviewManager'}"></el-menu-item>
              <el-menu-item index="4" :route="{path:'/'}"></el-menu-item>
              <el-menu-item index="5" :route="{path:'/'}"></el-menu-item>

            </el-menu>
          </el-aside>

          <el-main style="padding:0;margin-right:100px; min-height:600px;">
            <el-card style="margin:0 auto;">
              <!-- <el-table
                :data="topicList"
                style="width: 100%"
                header-row-style="background-color:#E4E7ED;color:#303133;"
                @row-click="selectTopic">
                <el-table-column
                  prop="title"
                  width="385"
                  label="">
                </el-table-column>
                <el-table-column
                  prop="publisher"
                  label="/"
                  width="200">
                </el-table-column>
                <el-table-column
                  prop="replyAndLook"
                  label="/"
                  width="100">
                </el-table-column>
                <el-table-column
                  prop="replyTime"
                  label="">
                </el-table-column>
              </el-table> -->
                <div v-for="esingle in topicList">
                  <router-link :to="'/TopicInfo/' + esingle.topicId" target="_blank">
                    <topic :topic="esingle"></topic>
                  </router-link>
                </div>
            </el-card>
          </el-main>
        </el-container>
      </el-main>
      <el-footer style="padding:0;">
        <myFooter></myFooter>
      </el-footer>
    </el-container>
  </div>
</template>
<script>
import topic from './Topic'
import axios from 'axios'
import config from './../../router/config'
import myHeader from './../public/Header'
import myFooter from './../public/Footer'
import hotSpotNews from './HotSpotTopic'
export default{
  components:{
    topic,
    myHeader,
    myFooter,
    hotSpotNews,
  },
  data(){
    return{
      rootURL: config.rootURL,
      activeIndex:0,
      topicList:[
        {
          publishId:123,
          publishTime:'2017-10-31 19:17:00',
          publisher:'',
          title:'',
          replyAndLook:'123/8796',
        },
        {
          publishId:124,
          publishTime:'2017-10-31 19:30:00',
          publisher:'',
          title:'',
          replyAndLook:'42/954',
        }
      ],
      tableData: [
        {
          date: '2016-05-02',
          name: '',
          address: ' 1518 '
        },
        {
          date: '2016-05-04',
          name: '',
          address: ' 1517 '
        },
        {
          date: '2016-05-01',
          name: '',
          address: ' 1519 '
        },
        {
          date: '2016-05-03',
          name: '',
          address: ' 1516 '
        }
      ]
    }
  },
  methods:{
    timeago(time){   //dateTimeStamp100013
            var minute=1000*60;      //
            var  hour=minute*60;
            var day=hour*24;
            var week=day*7;
            var halfamonth=day*15;
            var month=day*30;

            var  now=new Date().getTime();   //
            var dateTimeStamp = Date.parse(new Date(time));

            console.log('timeago'+now);
            console.log('timeago'+dateTimeStamp);
            var diffValue=now-dateTimeStamp;//

            if(diffValue<0){return;}

            var  minC=diffValue/minute;  //
            var  hourC=diffValue/hour;
            var  dayC=diffValue/day;
           var  weekC=diffValue/week;
            var  monthC=diffValue/month;
            var result;
            console.log(minC);
           if(monthC>=1){
             result=" "+parseInt(monthC)+""
              }

         else  if(weekC>=1){
             result=" "+parseInt(weekC)+""
              }
        else    if(dayC>=1){
             result=" "+parseInt(dayC)+""
              }
       else    if(hourC>=1){
             result=" "+parseInt(hourC)+""
              }
       else    if(minC>=1){
             result=" "+parseInt(minC)+""
              }
        else{
        result="";
        }
let that=this;
that.time=result;
     return result;
   },
    getTopicList(){
      let that = this ;
      axios.post(that.rootURL+'/getTopic.do').then(function(res){
        var topic={};
        that.topicList=[];
        for(that.idx of res.data){
          topic=[];
          topic.topicId=that.idx.topicId;
          topic.replyTime=that.idx.topicReplyTime;
          topic.publisher=that.idx.username
          topic.publishTime=that.timeago(that.idx.topicPublishTime);
          topic.title=that.idx.topicTitle;
          topic.readNum=that.idx.topicReadNum;
          topic.replyNum=that.idx.topicReplyNum;
          that.topicList.push(topic);
        }
      })
    },

  },
  created(){
    let that=this;
    that.getTopicList();
  }
}
</script>

<style scoped>

</style>



